<template>
  <div>
    <van-picker
      title=""
      show-toolbar
      :columns="columns"
      @confirm="onConfirm"
      @cancel="onCancel"
      @change="onChange"
    />
  </div>
</template>

<script>
export default {
  props: {
    gender: {
      required: true,
      tyep: Number
    }
  },
  data() {
    return {
      columns: ['', ''],
      index: 0
    }
  },
  methods: {
    async onConfirm(val) {
      //   
      if (val === '') {
        try {
          await this.$axios.patch('v1_0/user/profile', {
            gender: this.index
          })
          this.$toast.success('')
        } catch (err) {}
      }
      console.log(val)
      this.$emit('changeGender', val === '' ? 0 : 1)
    },
    onChange(p, v, i) {
      console.log(i)
      this.index = i
    },
    onCancel() {
      this.$emit('closeGender')
    }
  }
}
</script>

