<template>
  <div>
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <div class="field"></div>
    <van-field
      v-model.trim="message"
      rows="2"
      autosize
      label=""
      type="textarea"
      maxlength="10"
      placeholder=""
      show-word-limit
      border
    />
  </div>
</template>

<script>
export default {
  props: {
    value: {
      required: true,
      type: String
    }
  },
  data() {
    return {
      message: this.value
    }
  },
  methods: {
    async onClickRight() {
      if (this.message.length === 0) {
        return this.$toast.fail('')
      }
      await this.$axios.patch('v1_0/user/profile', {
        name: this.message
      })

      this.$emit('changeName', this.message)
    },
    onClickLeft() {
      this.$emit('closePop')
    }
  }
}
</script>

<style lang="less" scoped>
.field {
  padding: 10px;
}
</style>
